body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Open Sans', sans-serif !important;
  font-size: 14px !important;
  background-color: #17a2b8 !important;
}

.main-wrapper{
  display: flex;
  min-height: 100vh;
  align-items: center;
  /* min-height: 84vh; */
  /* margin: 2vh 0%; */
  
}
.wrapper{
  display: flex;
  /* height: 84vh; */
  /* overflow: auto; */
  /* background-color: white; */
  /* margin: auto; */
  border: 0 !important;
}

.left-side, .right-side{
  height: auto;
}

.left-side{
  position: relative;
}

.photo img{
  display: block;
  width: 100%;
  margin: 0 auto;
}

.social-media{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 7vh;
  align-items: center;
}

.identity{
  position: absolute;
  bottom: 3rem;
  width: 100%;
  font-size: 14px;
}

@media screen and (max-width: 600px){
  .main-wrapper{
      margin: 2vh 0%;
  }
  .photo {
      height: 385px;
  }
  .main-wrapper{
    display: flex;
    min-height: 80vh !important;
    align-items: center;
    /* min-height: 84vh; */
    /* margin: 2vh 0%; */
    
  }
  .data{
    display: flex;
    justify-content: space-around;
    width: 100% !important;
  }
  .left-data, .right-data{
    flex: 1;
  }
}
@media screen and (max-width: 468px){
  .data{
    display: flex;
    justify-content: space-around;
    width: 100% !important;
  }
  .left-data, .right-data{
    flex: 1;
  }
}

.socmeds{
  display: flex;
  list-style: none;
  justify-content: space-around;
  padding: 0 !important;
  margin: 0 !important;
}

.socmed{
  color: white;
  font-size: 1.3rem;
}

.socmed:hover{
  color: #17a2b8;
}

.right-side{
  
}

.navbar{
  background-color: #f3f2f2;
  padding: 0 !important;
  margin: 0 !important;
}

.navlinks{
  width: 100%;
}

.navlink{
  flex: 1;
  text-align: center;
  color: #6f6f6f;
  padding: 10px;
}

.navlink:hover{
  background: #e6e6e6;
  text-decoration: none;
  color: #17a2b8;
  transition: 0.3s;
}

.navlink-active{
  background: white;
  color: #17a2b8;
  font-weight: bold;
}

.navlink-active:hover{
  background: white;
  color: #17a2b8;
  font-weight: bold;
}

.main-container{
  display: flex;
  height: 76vh;
  overflow: auto;
  align-items: center;
  width: 100%;
}

.home-wrap{
  text-align: center;
}

hr.bold{
  background-color: #17a2b8;
    height: 2px;
    width: 20%;
    margin-bottom: 5vh;
    border-style: none;
}

.effect{
  animation: transisi .4s ease-in-out ;
}

@keyframes transisi {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.data{
  display: flex;
  justify-content: space-around;
  width: 70%;;
}

.left-data, .right-data{
  flex: 1;
}


